<template>

<div class="heading">
    <h1></h1>
</div>

<div class="item-all">
    <div class="item-left">
            <div class="form">
            <h3 class="form_title">  </h3>
            <form action="sendmail.php" method="POST" @submit.prevent="formSend()" id="form" class="form_body">
                <div class="form_item">
                    <label for="formName" class="form_label">'*:</label>
                    <input type="text" id="formName" v-model="firstName" name="firstname" class="form_input">
                </div>
                <div class="form_item">
                    <label for="formSurname" class="form_label">*:</label>
                    <input type="text" id="formSurname" v-model="lastName" name="lastname" class="form_input">
                </div>
                <div class="form_item">
                    <label for="formEmail" class="form_label"> *:</label>
                    <input type="text" id="formEmail" v-model="email" name="email" class="form_input">
                </div>
                <div class="form_item">
                    <label for="formMessage" class="form_label">*:</label>
                    <textarea name="message" id="formMessage" v-model="message" cols="30" rows="10" class="form_input"></textarea>
                </div>
                <button type="submit" class="form_button"></button>
            </form>
        </div>
    </div>
    <div class="item-right">
        <h3> </h3>
        <p>  ,      ,       .      ,    , ,          .</p>
        <p>     -  '  .</p>
        <p>-      ,         .           ,            .  ,    _    ,  ,   .           , , ,     .     !</p>
        <p>     ' .      -,            .      ,   , .</p>
    </div>
</div>
  
</template>

<script>
import { email, required } from '@vuelidate/validators'
import { useVuelidate } from '@vuelidate/core'
export default {
    setup: () => ({ v$: useVuelidate() }),
    data () {
        return {
            form: '#form',
            firstName: '',
            lastName: '',
            email: '',
            message: ''
        }
    },
    validations () {
        return {
            firstName: { required },
            lastName: { required },
            email: { email, required },
            message: { required }
        }
    },
    methods: {
        async formSend() {
            this.v$.$validate()
            if (!this.v$.$error) {
                form.classList.add('_sending');
                const formData = new FormData(form);
                const requestOptions = {
                    method: "POST",
                    body: formData
                };
                const response = await fetch('./sendmail.php', requestOptions);
                if (response.ok) {
                    let result = await response.json();
                    alert(result.message);
                    form.reset();
                    form.classList.remove('_sending');
                } else {
                    alert("");
                    form.classList.remove('_sending');
                }
            } else {
                alert('     ');
                }
		}
    }
}
</script>

<style>
</style>